.list-1 {
  font-family: "Montserrat";
}

.nav-custom {
  background-color: rgb(0, 0, 0);
  opacity: 0.95;
}

.main {
  background-image: url("../assets/images/cover.jpg");
  background-size: cover;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
}

.btn-custom {
  background-color: rgb(221, 67, 126);
  border-radius: 0;
  color: white;
  animation: zoominout 1s ease-in;
}

.btn-custom:hover {
  background-color: white;
  color: rgb(221, 67, 126);
}

.btn-custom::selection {
  background-color: white;
  color: rgb(221, 67, 126);
}

.divider {
  border-top: 1px solid white;
}

.divider2 {
  border-top: 1px solid rgb(221, 67, 126);
}

@keyframes zoominout {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
